<template>
	<view>
		<view class="login_area">
			<view class="login_phone">
				<input type="text" v-model="username" placeholder="请输入手机号" />
			</view>
			<view class="login_password">
				<input type="password"  v-model="password" placeholder="请输入验证码" />
			</view>
			<view class="login_info">
				<button @click="loginInfo">登录</button>
			</view>
		</view>
	</view>
</template>

<script>
	import userInfos from '../../static/dataJs/login.js'
	export default {
		data() {
			return {
				user: userInfos.userData[0],
				username:'',
				password:''
			}
		},
		methods: {
			loginInfo() {
				if (this.username == this.user.username && this.password == this.user.password){
					uni.showToast({
					    title: '登录成功',
					    duration:500
					});
					 this.$store.dispatch("AJAX_LOGIN", this.user);
					setTimeout(()=>{
						uni.reLaunch({
							url:'../tabbar/tabbar-5/tabbar-5'
						})
					},1000)
					
				}else{
					uni.showToast({
					    title: '密码或用户名错误',
					    duration: 2000
					});
				}
			}
		}
	}
</script>
<style>
	page {
		height: 100%;
		width: 100%;
		background-image: url('data:image/jpeg;base64,');
		background-repeat: no-repeat;
		background-size: cover;
	}
</style>

<style scoped>
	.login_area {
		width: 280px;
		height: 280px;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}

	.login_phone {
		width: 280px;
		height: 45px;
		background-color: #fff;
		margin-bottom: 10px;
		border-radius: 6px;
		padding: 10px 0;
		box-sizing: border-box;

	}

	.login_phone input {
		padding-left: 5px;
		box-sizing: border-box;
		background-color: #fff;
	}

	.login_password {
		width: 280px;
		height: 45px;
		border-radius: 6px;
		outline: none;
		padding: 10px 0;
		box-sizing: border-box;
		margin-bottom: 10px;
		background-color: #fff;
	}

	.login_password input {
		padding-left: 5px;
		box-sizing: border-box;
		background-color: #fff;
	}

	.login_info button {
		background-color: #ff536f;
		width: 270px;
		height: 45px;
		border-radius: 20px;
		color: #fff;
	}
</style>
